import React from "react";
import { useLocation } from "@reach/router";
import Header from "../../../componets/header/Header";
import Footer from "../../../componets/footer/Footer";
import { events } from "../../../data/events";
import { useLanguage } from "../../../context/LanguageContext";
import { getContent } from "../../../content/content";
import "../../../styles/checkout.css";

const SuccessPage: React.FC = () => {
  const location = useLocation();
  const { lang } = useLanguage();
  const content = getContent(lang).pages.landing.section.eventContent;
  const eventSlug = location.pathname.split("/")[2];
  const eventData = events.find(e => e.id === eventSlug);

  if (!eventData) {
    return (
      <main style={{ background: '#1A202C', minHeight: '100vh', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
        <Header />
        <div style={{ color: 'white', textAlign: 'center', padding: '2rem' }}>
          <h1 style={{ fontSize: '2rem', marginBottom: '1rem', color: '#D1A12A' }}>Event Not Found</h1>
          <p>The event you're looking for doesn't exist.</p>
        </div>
        <Footer />
      </main>
    );
  }

  return (
    <>
      <Header />
      <main className="checkout-page success">
        <div className="success-container">
          <div className="success-content">
            <div className="success-icon">🎉</div>
            <h1 className="success-title">{content.success.title}</h1>
            <p className="success-message">
              {content.success.message} <strong>{eventData.name}</strong>
            </p>
            
            <div className="success-details">
              <div className="detail-item">
                <span className="detail-label">{content.success.details.event}</span>
                <span className="detail-value">{eventData.name}</span>
              </div>
              <div className="detail-item">
                <span className="detail-label">{content.success.details.date}</span>
                <span className="detail-value">{new Date(eventData.date).toLocaleDateString()}</span>
              </div>
              <div className="detail-item">
                <span className="detail-label">{content.success.details.location}</span>
                <span className="detail-value">{eventData.location}</span>
              </div>
            </div>

            <div className="success-info">
              <p>{content.success.info.email}</p>
              <p>{content.success.info.save}</p>
            </div>

            <div className="success-actions">
              <a href={`/event/${eventSlug}`} className="btn-primary">
                {content.success.actions.backToEvent}
              </a>
              <a href={`/event/${eventSlug}/admission`} className="btn-secondary">
                {content.success.actions.viewAdmission}
              </a>
            </div>
          </div>
        </div>
      </main>
      <Footer />
    </>
  );
};

export default SuccessPage;